<template>
  <div id="Software">
    {{ title }}
  </div>
</template>
<script>
import axios from "axios";
export default {
  name: "Software",
  data() {
    return { title: "" };
  },
  mounted() {
    // console.log(axios);
    // axios
    //   .get("static/json/user.json", { a: 1989 })
    //   .then(res => {
    //     console.log(res);
    //   })
    //   .catch();
    fetch("static/json/user.json")
      .then(res => {
        return res.json();
      })
      .then(res => {
        console.log(res);
        this.title = res.title;
      });
  }
};
</script>
<style scoped></style>



